import globalVars from "../../../library/globalVariables";

const WebForm = ({fields}) => {
    

    const fieldsOptions = fields.map(field => {
        return <option>{field.fieldName}</option>
    })

    return (
        <>  
        <select name="fields">
        <option>{globalVars.blankOption}</option>
        {fieldsOptions}
        </select>
        </>
    )
}

export default WebForm;